import React, {memo, useCallback} from 'react';
import {ScrollableTabView} from '@true/ui';
import {SavedItemThumbnail} from '@/screens/ProfileScreen/SavedItemThumbnail';
import {requestSavedItems} from '@/store/saved/function';
import {getSavedByQuery, useSavedByQuery} from '@/store/saved';

interface SavedItemTabProps {
  index: number;
}

export const SavedItemTab = memo(function (props: SavedItemTabProps) {
  const {index} = props;
  const renderItem = useCallback(
    (item: any, index?: number, isLast?: boolean) => {
      return <SavedItemThumbnail id={item} />;
    },
    [],
  );
  return (
    <ScrollableTabView.List
      itemHeight={80}
      index={index}
      renderItem={renderItem}
      requestFn={requestSavedItems}
      getItemByQuery={getSavedByQuery}
      useItemByQuery={useSavedByQuery}
    />
  );
});
