import {memo, useCallback} from 'react';
import {ScrollableTabView} from '@true/ui';
import {getEventsByQuery, useEventsByQuery} from '@/store/events';
import {EventThumbnail} from '../EventScreen/EventThumbnail';
import {requestEventsProfile} from '@/store/events/function';

interface EventProfileProps {
  index: number;
  username: string;
}

export const EventProfile = memo(function (props: EventProfileProps) {
  const {index, username} = props;
  const renderItem = useCallback(
    (item: any, index?: number, isLast?: boolean) => {
      return <EventThumbnail id={item} />;
    },
    [username],
  );
  return (
    <ScrollableTabView.List
      itemHeight={400}
      index={index}
      params={{username}}
      renderItem={renderItem}
      requestFn={requestEventsProfile}
      getItemByQuery={getEventsByQuery}
      useItemByQuery={useEventsByQuery}
      query={username}
    />
  );
});
