import {memo} from 'react';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import Animated, {useAnimatedStyle} from 'react-native-reanimated';
import {Fonts, goBack, styled} from '@true/core';
import {IC_OUTLINED_BACKWARD, IC_OUTLINED_LEFT} from '@true/asset';
import {UIButton, UIText} from '@true/ui';

export const HeaderMyProfile = memo(function ({name}: {name: string}) {
  const {aTop} = useAnimatedSafeAreaInsets();
  const animatedPaddingTop = useAnimatedStyle(() => ({
    paddingTop: aTop.value,
  }));
  return (
    <SContainer style={animatedPaddingTop}>
      <UIButton source={IC_OUTLINED_LEFT} onPress={goBack} />
      <UIText.HeaderLarge>{name}</UIText.HeaderLarge>
    </SContainer>
  );
});
const SContainer = styled(Animated.View)`
  flex-direction: row;
  align-items: center;
  width: 100%;
  padding: 8px 16px;
  gap: 12px;
`;
