import { ScreenWrapper, ScrollableTabView } from "@true/ui";
import React, { memo } from "react";
import { HeaderMyProfile } from "@/screens/ProfileScreen/HeaderMyProfile";
import { Fonts, getUser, styled, useMe, useNavigationParams } from "@true/core";
import { ProfileInfoSection } from "@/screens/ProfileScreen/ProfileInfoSection";
import { ChannelProfile } from "@/screens/ProfileScreen/ChannelProfile";
import { EventProfile } from "@/screens/ProfileScreen/EventProfile";
import { HomeProfile } from "@/screens/ProfileScreen/HomeProfile";
import { SavedItemTab } from "@/screens/ProfileScreen/SavedItemTab";
import { IC_OUTLINED_LEFT } from "@true/asset";

export interface ProfileScreenParams {
  username: string;
  user_id: string | number | undefined;
}

export const ProfileScreen = memo(function() {
  const { username, user_id } = useNavigationParams<ProfileScreenParams>();
  const me = useMe();
  const user = getUser(user_id || 0);
  return (
    <ScreenWrapper isGrey={true}>
      <HeaderMyProfile name={user?.name || ""} />
      <ScrollableTabView.Provider
        tabs={[
          {
            title: "Home",
            content: p => <HomeProfile index={p.index} username={username} />,
            stickyHeaderRender: (
              <SContentContainer>
                <STextPinned>Recently updated</STextPinned>
                <SArrowDown source={IC_OUTLINED_LEFT} />
              </SContentContainer>
            )
          },
          {
            title: "Joined event",
            content: p => <EventProfile index={p.index} username={username} />
          },
          {
            title: "Joined channels",
            content: p => (
              <ChannelProfile index={p.index} username={username} />
            )
          },
          username === me?.username
            ? {
              title: "Saved items",
              content: p => <SavedItemTab index={p.index} />
            }
            : {
              title: "",
              content: p => null
            },
          username === me?.username
            ? {
              title: "Scheduled items",
              content: p => <SavedItemTab index={p.index} />
            }
            : {
              title: "",
              content: p => null
            }
        ]}>
        <ScrollableTabView.Content />
        <ScrollableTabView.CollapsableTabBar>
          <ProfileInfoSection data={user} />
        </ScrollableTabView.CollapsableTabBar>
      </ScrollableTabView.Provider>
      {/*<Index />*/}
    </ScreenWrapper>
  );
});

const SContentContainer = styled.View`
  flex-direction: row;
  align-items: center;
  padding: 12px 16px;
  background-color: ${p => p.theme.neutral8};
`;
const STextPinned = styled.Text`
  font-family: ${Fonts.Regular};
  
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #242424;
  margin-right: 4px;
`;

const SArrowDown = styled.Image`
  width: 12px;
  height: 12px;
  transform: rotate(90deg);
  tint-color: ${p => p.theme.neutral1};
`;
export default ProfileScreen;
