import {memo, useCallback} from 'react';
import {getUpdatesByQuery, useUpdatesByQuery} from '@/store/updates';
import {requestUserProfile} from '@/store/updates/function';
import {ScrollableTabView} from '@true/ui';
import {PostThumbnail} from '@/components/PostThumbnail';
import {styled} from '@true/core';
import {Fonts} from '@true/core';

interface HomeProfileProps {
  index: number;
  username: string;
}

export const HomeProfile = memo(function (props: HomeProfileProps) {
  const {index, username} = props;
  const renderItem = useCallback(
    (item: string | number, index?: number, isLast?: boolean) => {
      return <PostThumbnail isInFeed={true} id={item as number} />;
    },
    [username],
  );
  return (
    <ScrollableTabView.List
      itemHeight={400}
      index={index}
      params={{username}}
      renderItem={renderItem}
      requestFn={requestUserProfile}
      getItemByQuery={getUpdatesByQuery}
      useItemByQuery={useUpdatesByQuery}
      query={username}
    />
  );
});
