import {memo, useCallback} from 'react';
import {ScrollableTabView} from '@true/ui';
import {getChannelsByQuery, useChannelsByQuery} from '@/store/channels';
import {ChannelThumbnail} from '@/screens/ChannelScreen/ChannelThumbnail';
import {requestUserProfile} from '@/store/updates/function';
import {styled} from '@true/core';

interface ChannelProfileProps {
  index: number;
  username: string;
}

export const ChannelProfile = memo(function (props: ChannelProfileProps) {
  const {index, username} = props;
  const renderItem = useCallback(
    (item: any, index?: number, isLast?: boolean) => {
      return <ChannelThumbnail id={item} />;
    },
    [username],
  );
  return (
    <SContentWrapper>
      <ScrollableTabView.List
        index={index}
        params={{username}}
        numColumns={2}
        itemHeight={220}
        renderItem={renderItem}
        requestFn={requestUserProfile}
        getItemByQuery={getChannelsByQuery}
        useItemByQuery={useChannelsByQuery}
        query={username}
      />
    </SContentWrapper>
  );
});
const SContentWrapper = styled.View`
  flex: 1;
  //padding: 0 16px;
  margin-left: 16px;
`;
