import {Fonts, styled, ToastService, useBoolean, useTheme} from '@true/core';
import {memo, useCallback} from 'react';
import {View} from 'react-native';
import {
  IC_OUTLINED_FORUM,
  IC_OUTLINED_SCHEDULE,
  IC_OUTLINED_CLOSE,
} from '@true/asset';
import {useSaved} from '@/store/saved';
import moment from 'moment';
import {
  navigateToDetailPostScreen,
} from '@/utils';
import {ConfirmationDialog} from '@true/ui';
import {requestToggleSaved} from '@/store/saved/function';
import {globalStyles} from '@/utils/globalStyle';
import {FlexCenter} from '@/utils/helper';

interface SavedItemThumbnailProps {
  id: number;
}

export const SavedItemThumbnail = memo(function (
  props: SavedItemThumbnailProps,
) {
  const {id} = props;
  const data = useSaved(id);
  const [visible, show, hide] = useBoolean();
  const theme = useTheme();
  const onNavigateToDetail = useCallback(() => {
    navigateToDetailPostScreen({
      id: id,
      user_id: data?.user_id || 0,
      time: data?.since || 0,
      hid: data?.hid || '',
    });
    return;
  }, [data]);
  const onUnSaved = useCallback(async () => {
    await requestToggleSaved({
      hid: data?.hid || '',
      status: -1,
    });
    ToastService.show('Unsaved successfully');
  }, [data]);
  return (
    <SContainer>
      <ConfirmationDialog
        isVisible={visible}
        onCloseRequest={hide}
        title={'Unsaved item'}
        content={'Are you sure you want to unsaved this item?'}
        buttons={[
          {text: 'Close'},
          {
            text: 'Confirm',
            color: '#FFFFFF',
            backgroundColor: theme.primaryColor,
            onPress: onUnSaved,
          },
        ]}
      />
      <SContentContainer>
        <View style={globalStyles.mr8}>
          <SChatIcon source={IC_OUTLINED_FORUM} />
        </View>
        <SInfoView onPress={onNavigateToDetail}>
          <STitle>{data?.name || 'No title'}</STitle>
          <FlexCenter>
            <STimeIcon source={IC_OUTLINED_SCHEDULE} style={globalStyles.mr4} />
            <STextNormal>
              {`Item saved at ${moment(
                // @ts-ignore
                data?.['saved_at'] * 1000,
              ).format('dddd HH:mm DD/MM/YYYY')}`}
            </STextNormal>
          </FlexCenter>
        </SInfoView>
        <SIcon onPress={show}>
          <SImage source={IC_OUTLINED_CLOSE} />
        </SIcon>
      </SContentContainer>
    </SContainer>
  );
});
const SContainer = styled.View`
  padding: 0 16px;
`;
const SContentContainer = styled.View`
  background-color: white;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-radius: 8px;
  margin-top: 8px;
  padding: 12px 0 12px 16px;
`;
const SInfoView = styled.TouchableOpacity`
  padding: 16px 0;
  flex: 1;
`;
const SIcon = styled.TouchableOpacity`
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
`;
const SImage = styled.Image`
  width: 16px;
  height: 16px;
`;

const STitle = styled.Text`
  font-family: ${Fonts.Regular};

  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #242424;
`;
const STextNormal = styled.Text`
  font-family: ${Fonts.Regular};

  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #666666;
`;
const SChatIcon = styled.Image`
  width: 28px;
  height: 28px;
`;
const STimeIcon = styled.Image`
  width: 16px;
  height: 16px;
`;
